@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");
@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon/fonts/icomoon.eot?za5w69");
  src: url("../fonts/icomoon/fonts/icomoon.eot?za5w69#iefix") format("embedded-opentype"), url("../fonts/icomoon/fonts/icomoon.ttf?za5w69") format("truetype"), url("../fonts/icomoon/fonts/icomoon.woff?za5w69") format("woff"), url("../fonts/icomoon/fonts/icomoon.svg?za5w69#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
/*--------------------adjustment----------------------*/
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  font-size: 14px;
  min-width: 320px;
  position: relative;
  line-height: 17px;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: rgba(0, 0, 0, 0.8);
}
.lock body {
  overflow: hidden;
  touch-action: none;
  overflow-behavior: none;
}

ul, li {
  margin: 0;
  padding: 0;
}

input input[type=text],
input[type=email],
input[type=tel],
textarea {
  appearance: none;
}

.input,
input {
  outline: none;
  display: block;
  width: 100%;
}

textarea.input {
  resize: none;
  padding: 0 0;
}

.wrapper {
  margin: 0 auto;
  padding-left: 24px;
  padding-right: 24px;
  max-width: 1440px;
  outline: 1px solid;
}

.wrapper-smart {
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 24px;
  padding-right: 24px;
  outline: 1px solid;
}

.wrapper-flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: -8px;
  margin-right: -8px;
}

.column {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 8px;
  margin-right: 8px;
  width: calc(25% - 16px);
}

.hidden {
  display: none;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 52.25%;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

._prod-img {
  position: relative;
  padding-bottom: 80%;
}
._prod-img img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

._fit {
  padding-bottom: 80%;
  position: relative;
}

._fit ._fit-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
}

@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)) {
  .bd-filter-fallback {
    background-color: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(31px);
  }
}

/*--------------------form----------------------*/
.form__row:not(:last-child) {
  margin-bottom: 20px;
}
.form__row-content {
  padding: 22px;
  background-color: #F1F1F1;
}
.form__row-content .form__custom-policy:not(:last-child) {
  margin-bottom: 22px;
}
.form__row-title {
  flex: 0 0 auto;
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.8);
}
.form__label {
  display: block;
  position: relative;
}
.form__label--phone {
  max-width: 304px;
}
.form__label:not(:last-child) {
  margin-bottom: 8px;
}
.form__label--error .form__input {
  color: #E63600;
  border-color: #E63600;
}

.form__input {
  height: 40px;
  padding: 12px;
  border: 1px solid #000;
  color: rgba(0, 0, 0, 0.8);
  background: #FFFFFF;
  border: 1px solid #C4C4C4;
  border-radius: 4px;
  font-family: "Inter", sans-serif;
}
.form__input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input::-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input:-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input--bg {
  background-color: #F1F1F1;
  border-color: transparent;
  border-radius: 0;
}

.form__custom-policy {
  display: flex;
  align-items: flex-start;
}

.form__custom {
  position: relative;
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.form__check {
  position: absolute;
  box-sizing: border-box;
  width: 1px;
  height: 1px;
  display: block;
  left: 50%;
  top: 100%;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.form__check:checked + .form__custom-chek {
  border-color: #003D9E;
}
.form__check:checked + .form__custom-chek:before {
  display: block;
}
.form__check:checked ~ .form__policy {
  color: rgba(0, 0, 0, 0.8);
}

.form__custom-chek {
  margin-right: 13px;
  display: block;
  position: relative;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(0, 0, 0, 0.8);
  border-radius: 50%;
}
@media (any-hover: hover) {
  .form__custom-chek:hover {
    cursor: pointer;
  }
}
.form__custom-chek:before {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: #003D9E;
  content: "";
}

.form__area {
  width: 100%;
  height: 72px;
  padding: 12px 9px;
  border: none;
  background: #F1F1F1;
  padding-left: 20px;
  color: #222222;
  font-family: "Inter", sans-serif;
}
.form__area::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__area::-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__area:-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__area:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__area::-webkit-resizer {
  width: 28px;
  height: 28px;
  border-style: solid;
  border-width: 4px 0 4px 8px;
  border-color: transparent transparent transparent #C4C4C4;
}
.form__area:hover, .form__area:active, .form__area:focus {
  outline: none;
}

.form__policy {
  color: rgba(0, 0, 0, 0.4);
}

/*--------------------END form----------------------*/
/*--------------------END adjustment----------------------*/
/*--------------------ui----------------------*/
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  max-width: 270px;
  min-width: 270px;
  color: #fff;
  text-decoration: none;
  background-color: #003D9E;
  padding: 5px 15px;
  font-size: 12px;
  line-height: 17px;
  text-transform: uppercase;
  border: 1px solid transparent;
  border-radius: 20px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color 0.1s ease;
}
.btn:hover {
  background-color: #fff;
  border-color: #003D9E;
  color: #003D9E;
}
.btn:active {
  background-color: #fff;
  border-color: #003D9E;
  color: #003D9E;
}
.btn-empty {
  border-color: #003D9E;
  color: #003D9E;
  background-color: #fff;
}
.btn-empty:hover, .btn-empty:active {
  background-color: #003D9E;
  border-color: transparent;
  color: #fff;
}
.btn-gray {
  color: #fff;
  background-color: #B7B7B7;
}
.btn-gray:hover, .btn-gray:active {
  border-color: #B7B7B7;
  background-color: #fff;
  border-color: #B7B7B7;
  color: #B7B7B7;
}
.btn-red {
  color: #fff;
  background-color: #E63600;
}
.btn-red:hover, .btn-red:active {
  border-color: #E63600;
  background-color: #fff;
  color: #E63600;
}
.btn-yellow {
  color: #fff;
  background-color: #FCC200;
}
.btn-yellow:hover, .btn-yellow:active {
  border-color: #FCC200;
  background-color: #fff;
  color: #FCC200;
}
.btn-sm {
  min-height: 25px;
  min-width: 125px;
}
.btn-fw {
  width: 100%;
}
@media (any-hover: hover) {
  .btn:hover {
    cursor: pointer;
  }
}

.billet {
  height: 24px;
  display: inline-block;
  padding: 5px 9px 5px 6px;
  border-radius: 0 20px 20px 0;
  font-size: 10px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
.billet--new {
  background-color: #13B3EB;
}
.billet--markdown {
  background-color: #FCC200;
}
.billet--action {
  background-color: #E63600;
}

/*--------------------END ui----------------------*/
/*--------------------cart-page__left----------------------*/
.praongallery {
  box-sizing: border-box;
}
.praongallery * {
  box-sizing: border-box;
}

.praongallery__slider.slick-dotted {
  margin-bottom: -7px;
}
.praongallery__slider .slick-prev,
.praongallery__slider .slick-next {
  width: 32px;
  height: 32px;
  top: 190px;
}
.praongallery__slider .slick-prev {
  left: -30px;
}
.praongallery__slider .slick-next {
  right: -30px;
}
.praongallery__slider .slick-prev:before, .praongallery__slider .slick-next:before {
  content: "";
  width: 32px;
  height: 32px;
  display: block;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
}
.praongallery__slider .slick-next:before {
  background-image: url(../img/static/knportal_base/img/a-next.png);
}
.praongallery__slider .slick-prev:before {
  background-image: url(../img/static/knportal_base/img/a-prev.png);
}
.praongallery__slider .slick-dots {
  margin-top: 14px;
  position: static;
  display: flex;
  margin-left: -3.5px;
  margin-right: -3.5px;
}
.praongallery__slider .slick-dots li {
  width: auto;
  height: auto;
  margin: 0;
  display: block;
  margin-left: 3.4px;
  margin-right: 3.4px;
  margin-bottom: 7px;
}
.praongallery__slider .slick-dots li a {
  width: 98px;
  height: 80px;
  display: block;
}
.praongallery__slider .slick-dots li a img {
  width: 100%;
  cursor: pointer;
  object-fit: cover;
  object-position: center;
  height: 100%;
  border-radius: 5px;
}

.praongallery__slide {
  height: 379px;
  text-align: center;
}
.praongallery__slide img {
  max-height: 100%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------END cart-page__left----------------------*/
.header .sidebar-menu {
  display: flex;
}
.header .sidebar-menu .dl-submenu {
  opacity: 0;
  visibility: hidden;
}

/*--------------------_right-sidebar----------------------*/
.right-info .author div {
  font-size: 12px;
}
.right-info .num-short .btn {
  font-size: 14px;
  text-align: center;
  box-sizing: border-box;
  min-width: 180px;
  background-color: rgba(0, 42, 147, 0.7);
  color: #fff;
  font-weight: 900;
}

.wrap-new-main__second-box ul li {
  margin-top: 0;
}
.wrap-new-main__second-box ul li:not(:last-child) {
  margin-bottom: 10px;
}
.wrap-new-main__second-box ul li a {
  color: #3E3E3E;
}

.map-toggle {
  margin-top: 11px;
  font-size: 14px;
  color: #139E19;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

/*--------------------END _right-sidebar----------------------*/